.contact{
    display: block;
    width: 100%;
    height: 700px;;
    background-color: rgba(0, 0,0, .7);
    background-image: url("../img/map-image.png");
    background-repeat: no-repeat;
    background-position: center;
    background-position: cover;
}
.conhead{
    
padding: 60px;
color: aliceblue;
text-align: center;

}
.conin{
    font-size: 20px;
    width: 400px;
    height: 50px;
    border-radius: 4px;
    border: 2px solid #fff;
    margin-left: 70px;
    margin-bottom: 25px;
    padding-left: 10px;
}
.conin:hover{
   
    border: 3px solid rgba(5, 145, 238, 0.7);
   
}

.textareacont{
    position: relative;
    top:-243px;
    left: 550px;;
    font-size: 20px;
    width: 400px;
    height: 210px;
    border-radius: 4px;
    border: 2px solid #fff;
    
    padding-left: 10px;
}
.textareacont:hover{
    border: 3px solid rgba(5, 145, 238, 0.7);
}
.contbutton {
    
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s; 
    transition-duration: 0.4s;
    cursor: pointer;
  }

.button2 {
    background-color: rgba(7, 127, 131, 0.411); 
    color:white; 
    border: 2px solid #008CBA;
  }
  
  .button2:hover {
    background-color: #008CBA;
    color: white;
  }
.contbtn{
    position: relative;
    top: -220px;
    text-align: center;
}

