.about{
    background-color: rgb(192, 193, 195);
    padding: 20px;
   
    
}
.team-member {
    margin-bottom: 50px;
    text-align: center;
  }
  
  .team-member img {
    width: 225px;
    height: 225px;
    border: 7px solid #fff;
  }
  
  .team-member h4 {
    margin-top: 25px;
    margin-bottom: 0;
    text-transform: none;
  }
  
  .team-member p {
    margin-top: 0;
  }

  .team-member img{
      width: 28%;
      height: 30%;
      border-radius: 50%;
      
  }
  ul{
      
      list-style: none;
  }
 ul li i{
    display: inline;
    padding: 2px;
  }
  .social li{
    display: inline;
    align-items: center;
    margin: 3px;
  }
  .social li i{
  text-align: inherit;
  }
 .social a{

  display: inline-block; 
  
  width: 25px;
  height: 25px;
 border-radius: 50%; 
 padding: 8px;
 background-color: black;
 color: #fff;
 
 
 }
 .social a:hover{
   color: rgb(73, 187, 197);
 }