
.resume{
    background-image: url("../img/resumebg.png") ;
    background-position: center;
    background-size: cover;
    
    height: 680px;
    color: aliceblue;
    padding: 20px;
}
.resprag{
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 15px;
    font-size: 30px;
}
.resph1{
    margin-top: 210px;
    font-size: 30px;
    margin-left: 10%;
}

.resbutton {
    
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s; 
    transition-duration: 0.4s;
    cursor: pointer;
    margin-left: 12%;
  }
  .resbutton a{
      text-decoration: none;
      color: aliceblue;
  }
  
  
  .resbutton2 {
    background-color:rgba(30, 57, 45, .7); 
    color: white; 
    border: 2px solid rgb(186, 143, 0);
  }
  
  .resbutton2:hover {
    background-color: rgba(186, 143, 0,.5);
    color: white;
  }
  @media screen and (max-width: 768px) {

    .resph1{
        margin-top: 210px;
        font-size: 20px;
        margin-left: 1%;
    }
    
    .resbutton {
        
        border: none;
        color: white;
        padding: 16px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 2px;
        -webkit-transition-duration: 0.4s; 
        transition-duration: 0.4s;
        cursor: pointer;
        margin-left: 2%;
      }
      .resbutton2 {
        background-color:rgba(30, 57, 45, .7); 
        color: white; 
        border: 2px solid rgb(186, 143, 0);
      }
      
      .resbutton2:hover {
        background-color: rgba(186, 143, 0,.5);
        color: white;
      }
  }