.detailcon{
    background-color: rgba(1, 2, 2, 0.753);
    padding: 10px;
   
}


.card-body{
    text-align: center;
    width: 28%;
    height: 150px;
    background-color: aliceblue;
    padding: 10px;
    margin: 10px;
    display: inline-block;
    border: 1px solid rgb(36, 176, 241);
    border-radius: 4px;
    
    
}
.card-body:hover{
    cursor: pointer;
    border: 3px solid rgb(12, 142, 202);


}
.detline{
width: 30%;
height: 5px;
background-color: rgb(13, 173, 106);
border: 4px;
margin-left: 35%;
}
.dfooter{
    padding: 30px;
text-align: center;
color: aliceblue;
}

@media screen and (max-width: 768px) {

    .card-body{
        text-align: center;
        width: 90%;
        height: 150px;
        background-color: aliceblue;
        padding: 10px;
        margin: 10px;
        display: block;
        border: 1px solid rgb(36, 176, 241);
        border-radius: 4px;
        
        
    }
    .card-body:hover{
        cursor: pointer;
        border: 3px solid rgb(12, 142, 202);
    
    
    }
    .detline{
    width: 30%;
    height: 5px;
    background-color: rgb(13, 173, 106);
    border: 4px;
    margin-left: 35%;
    }
    
    

}